import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import DAODevelopmentScreen from "../screens/dao-development/dao-development-screen";

const DAODevelopmentPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "DAO Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <DAODevelopmentScreen db={db} />
      </main>
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="DAO Development Company | Justtry Technologies"
    headTitle="DAO Development Company | DAO Development Services"
    description="We are a leading DAO Development Service Provider in India, providing smart contract, utility/governance token creation, and platform development."
    keywords="DAO Development, DAO platform Development, DAO Development Services, DAO Development Company, DAO Development Solutions, Decentralized Autonomous Organization Development Company, Decentralized Autonomous Organization Development, Decentralized Autonomous Organization"
  ></Seo>
);

export default DAODevelopmentPage;
export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
