import React from "react";
import { Container, Row, Tab, Col, Nav } from "react-bootstrap";
import TalkToUsNew from "../../components/common/talk-to-us-new";
import { Link } from "gatsby";
const DAOService = (props) => {
  const { db } = props;
  return (
    <>
      <Container>
        <div className="web_dev_tabs_wrapper">
          <Row>
            <h1 className="text-center">Our DAO Development Services</h1>
          </Row>
          <Tab.Container id="left-tabs-example" defaultActiveKey="one">
            <Row className="smart_ct_row web_dev_tabs">
              <Col md={4} sm={12} className="d-flex justify-content-center">
                <Nav variant="pills">
                  <Nav.Item>
                    <Nav.Link eventKey="one">DAO Consulting Service</Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="two">
                      DAO Smart Contract Development
                    </Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="three">DAO dApp Development</Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="four">DAO Node Development</Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="five">DAO Integration Service</Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="six">DAO Token Development</Nav.Link>
                  </Nav.Item>
                </Nav>
              </Col>
              <Col md={8} sm={12}>
                <Tab.Content>
                  <Tab.Pane eventKey="one">
                    <div className="web_dev_tab_content">
                      <h3>DAO Consulting Service</h3>
                      <p>
                        Elevate your decentralized ambitions with our DAO
                        consulting service. Our seasoned experts guide you
                        through the intricate landscape of Decentralized
                        Autonomous Organizations, offering strategic insights,
                        risk assessments, and tailored solutions to empower your
                        project's success.
                      </p>
                      <div className="crypto_btns_grp">
                        <Link className="prime_submit_btn" to="/contact-us">
                          View Live Demo
                        </Link>
                        <button className="second_consult_btn" type="button">
                          <TalkToUsNew label="Free Consult" db={db} />
                        </button>
                      </div>
                    </div>
                  </Tab.Pane>
                  <Tab.Pane eventKey="two">
                    <div className="web_dev_tab_content">
                      <h3>DAO Smart Contract Development</h3>
                      <p>
                        Unleash the full potential of your decentralized vision
                        with our DAO smart contract development. Our proficient
                        team crafts secure, efficient, and audited smart
                        contracts, ensuring seamless execution of governance
                        protocols, transparent decision-making, and trustless
                        interactions within your DAO ecosystem.
                      </p>
                      <div className="crypto_btns_grp">
                        <Link className="prime_submit_btn" to="/contact-us">
                          View Live Demo
                        </Link>
                        <button className="second_consult_btn" type="button">
                          <TalkToUsNew label="Free Consult" db={db} />
                        </button>
                      </div>
                    </div>
                  </Tab.Pane>
                  <Tab.Pane eventKey="three">
                    <div className="web_dev_tab_content">
                      <h3>DAO dApp Development</h3>
                      <p>
                        Transform your DAO into a dynamic and user-friendly
                        ecosystem with our dApp development services. We merge
                        cutting-edge technologies to create intuitive interfaces
                        and robust functionalities, fostering user engagement
                        and participation while amplifying the effectiveness of
                        decentralized governance.
                      </p>
                      <div className="crypto_btns_grp">
                        <Link className="prime_submit_btn" to="/contact-us">
                          View Live Demo
                        </Link>
                        <button className="second_consult_btn" type="button">
                          <TalkToUsNew label="Free Consult" db={db} />
                        </button>
                      </div>
                    </div>
                  </Tab.Pane>
                  <Tab.Pane eventKey="four">
                    <div className="web_dev_tab_content">
                      <h3>DAO Node Development</h3>
                      <p>
                        Strengthen the foundation of your DAO network with our
                        node development services. We architect and optimize
                        nodes to enhance network resilience, scalability, and
                        overall performance. Whether you're establishing a new
                        DAO or upgrading an existing one, our node development
                        ensures a robust and reliable decentralized
                        infrastructure.
                      </p>
                      <div className="crypto_btns_grp">
                        <Link className="prime_submit_btn" to="/contact-us">
                          View Live Demo
                        </Link>
                        <button className="second_consult_btn" type="button">
                          <TalkToUsNew label="Free Consult" db={db} />
                        </button>
                      </div>
                    </div>
                  </Tab.Pane>
                  <Tab.Pane eventKey="five">
                    <div className="web_dev_tab_content">
                      <h3>DAO Integration Service</h3>
                      <p>
                        Seamlessly integrate your DAO with external systems and
                        blockchain networks using our DAO integration service.
                        We specialize in connecting your decentralized ecosystem
                        with other applications, platforms, and services,
                        facilitating interoperability and expanding the reach
                        and influence of your DAO.
                      </p>
                      <div className="crypto_btns_grp">
                        <Link className="prime_submit_btn" to="/contact-us">
                          View Live Demo
                        </Link>
                        <button className="second_consult_btn" type="button">
                          <TalkToUsNew label="Free Consult" db={db} />
                        </button>
                      </div>
                    </div>
                  </Tab.Pane>
                  <Tab.Pane eventKey="six">
                    <div className="web_dev_tab_content">
                      <h3>DAO Token Development</h3>
                      <p>
                        Forge the backbone of your decentralized governance with
                        our DAO token development expertise. Our team designs
                        and deploys custom tokens tailored to your project's
                        specific needs, enabling secure and efficient value
                        transfer, voting mechanisms, and incentives within your
                        DAO community. Embrace the future of decentralized
                        finance with confidence.
                      </p>
                      <div className="crypto_btns_grp">
                        <Link className="prime_submit_btn" to="/contact-us">
                          View Live Demo
                        </Link>
                        <button className="second_consult_btn" type="button">
                          <TalkToUsNew label="Free Consult" db={db} />
                        </button>
                      </div>
                    </div>
                  </Tab.Pane>
                </Tab.Content>
              </Col>
            </Row>
          </Tab.Container>
        </div>
      </Container>
    </>
  );
};

export default DAOService;
